* {
    font-family: monospace;
}

div.container {
    display: grid;
    grid-template-columns: repeat(3);
    gap: 5px;
    grid-auto-rows: minmax(100px, auto);
    padding-left: 5px;
    padding-right: 5px;
}

div.header {
    display: flex;
    flex-direction: column;
    justify-content: left;
    list-style: none;
    grid-column: 1/3;
    grid-row: 1;
    margin-left: 3px;
    margin-right: 3px;
}

div.nav-links {
    display: flex;
    align-content: right;
    justify-content: right;
    grid-column: 3;
    grid-row: 1;
}

div.controls {
    grid-column: 1;
    grid-row: 2 / 4;
    margin-left: 15px;
    margin-top: 35px;
}

canvas.game-canvas {
    grid-column: 2;
    grid-row: 4;
}

button#run-game {
    font-family: monospace;
    font-size: 20px;
    grid-column: 2;
    grid-row: 1;
    width: 80px;
    height: 45px;
    margin: auto;
    border-radius: 100px;
}

#score {
    grid-column: 3;
    grid-row: 2;
    font-size: 26px;
    font-style: oblique;

}

h1 {
    font-size: 40px;
    font-family: monospace;
    color: black;
}

ul.nav-links > li {
    display: flex;
    list-style: none;
    align-content: right;
}

span i {
    font-size: 80px;
    justify-content: left;
}

ul.nav-links i {
    font-size: 50px;
}

div#controls li {
    list-style: disc;
    display: flex;
    font-size: 18px;
    padding: 25px;
    font-weight:100;
    margin: auto;
}

div#welcome {
    display: flex;
    grid-column: 2;
    grid-row: 2/3;
    // margin: auto;
    margin-left: 180px;
    margin-right: 180px;
    text-align: center;
    padding: 30px;
    font-size: 20px;
}



.hidden {
    display: none;
}